




















import Vue from 'vue';

export default Vue.extend({
	props: {
		value: Boolean,
		disabled: {
			type: Boolean,
			default: false
		},
		onText: {
			type: String,
			default: 'On'
		},
		offText: {
			type: String,
			default: 'Off'
		}
	},
	data() {
		return {
			state: true,
			id: Math.random()
				.toString(36)
				.slice(7)
		};
	},
	created() {
		this.state = this.value;
	}
});
